import { createMuiTheme, CssBaseline, ThemeProvider, useMediaQuery } from "@material-ui/core";
import React from "react";
import {
  Switch,
  Route,
  HashRouter,
  Redirect
} from "react-router-dom";
import PageFooterComponent from "./components/PageFooterComponent";
import PageHeaderComponent from "./components/PageHeaderComponent";
import TimetableComponent from "./components/timetable/TimetableComponent";

export default function App() {
  const prefersDarkMode = useMediaQuery('(prefers-color-scheme: dark)');
  // const prefersDarkMode = true;

  const theme = React.useMemo(() => createMuiTheme({
    palette: {
      type: prefersDarkMode ? 'dark' : 'light',
    },
    overrides: {
      MuiCssBaseline: {
        '@global': {
          body: {
            '-webkit-text-size-adjust': 'none',
            'text-size-adjust': 'none',
            '-moz-text-size-adjust': 'none',
            '-ms-text-size-adjuist': 'none',
          },
          '*': {
            '--scrollbarBG': '#CFD8DC',
            '--thumbBG': '#90A4AE',
            scrollbarWidth: 'thin',
            scrollbarColor: 'var(--thumbBG) var(--scrollbarBG)',
          },
          '*::-webkit-scrollbar': {
            width: '11px',
          },
          '*::-webkit-scrollbar-track': {
            background: 'var(--scrollbarBG)',
          },
          '*::-webkit-scrollbar-thumb': {
            backgroundColor: 'var(--thumbBG)',
            bordeRadius: '6px',
            border: '3px solid var(--scrollbarBG)',
          }
        }
      }
    }
  }), [prefersDarkMode]);

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <HashRouter>
        <Switch>
          <Route path="/timetable/:classParam">
            <div style={{ display: 'flex', flexDirection: 'column', height: '100vh' }}>
              <div style={{ flex: "1 0 auto" }}>
                <PageHeaderComponent />
                <TimetableComponent />
              </div>
              <PageFooterComponent />
            </div>
          </Route>
          <Route path="/">
            <Redirect to="/timetable/1A" />
          </Route>
        </Switch>
      </HashRouter>
    </ThemeProvider >
  );
}
